* {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 50px;
}

.nav__menu {
  font-size: 12px;
  font-weight: 500;
  text-align: center;
}

.nav__link {
  text-decoration: none;
  color: black;
  text-transform: uppercase;
  display: block;
  line-height: 60px;
  position: relative;
}

ul {
  list-style-type: none;
  display: flex;
}

li {
  display: inline-block;
  margin: 0 8px;
}

.is-active {
  color: #00acdc;
}

.nav__link:hover {
  color: #00acdc;
}

.is-active::after {
  content: '';
  display: block;
  background-color: #00acdc;
  width: 100%;
  height: 4px;
  border-radius: 8px;
  position: absolute;
  bottom: 0;
}

img {
  width: 100%;
  height: 100%;
}

.linklogo {
  width: 40px;
  height: 40px;
}

ul,
li {
  margin: 0;
}

.nav_items li:first-child {
  margin-left: 0;
}

.nav_items li:last-child {
  margin-right: 0;
}

.nav__item {
  margin-right: 20px;
}
